@import url('https://fonts.googleapis.com/css?family=Inconsolata|Fjalla+One');



$color1: #F79238;
$color2: #FFFFFF;
//$color3: #CBF3F0;
$color3: #935116;
// other test colors
$color4: #784212;
// #784212
$color5: #873600;
// #873600


$navSize: 70px;





/*whole page stuffs*/

body {
  background: $color2;
  box-sizing: border-box;
  font-family: 'Fjalla One', sans-serif;
}



a {
  color: black;
  text-decoration: none;
}



/* Nav Bar section */

header {
  background: rgba(0, 0, 0, 0.50);
  height: $navSize;
  width: 100%;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: center;
  transition: all .5s ease-in;
  
  .nav {
    width: 70vw;
    min-width: 875px;
    max-width: 1050px;
    height: 100%;
    display: flex;
    justify-content: space-around;
  }
  
  .menu {
    height: 100%;
    width: 100%;
    display: flex;
    /*align-items: center;*/
    justify-content: space-between;
  }
  
  .menu-wrapper {
    display: flex;
    flex-direction: row;
  }
  
  .menu-item {
    padding: 0 10px;
    border-right: solid;
    border-right-width: 4px;
    border-image:
            linear-gradient(to left, black, rgba(0, 0, 0, 0)) 1 100%;
    
  }
  
  #home {
    
  }
  #contact {
    
  }
  
  .logo-large {
    height: 205px;
    transition: height .5s linear;
    //content: url("../img/orangeLogo.png");
  }
  .logo-small {
    height: ($navSize - 2px);
    width: auto;
    transition: height .5s linear;
    animation: 1s linear 1;
    //content: url("../img/blackLogo.png");
  }
  
  
  #phone {
    margin: 7px 10px 0px 0px;
    position: absolute;
    top: 0;
    right: 0;
  }
  
}



.scroll-menu {
  background-color: $color1;
  transition: all .5s ease-in;
}



/*View section*/

.view {
  margin-top: $navSize;
}


.landing-wrapper {
  background: rgba(0, 0, 0, 0.89);
}

.landing-image {
  width: 100vw;
  height: 60vh;
  background: no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}


/* info */

.info-wrapper {
  margin-top: -50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  
  h1 {
    margin: 0;
    padding: 10px 0;
    text-align: center;
  }
  .inner {
    width: 70vw;
    max-width: 1000px;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
}



.outer-hour-wrapper {
  border-radius: 20px;
  background: $color3;
  width: 300px;
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}



.hour-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}



.day-list {
  padding: 0;
  height: auto;
  width: auto;
}



.day-list-item {
  display: flex;
}



.day {
  width: 150px;
}



.contact-wrapper {
  border-radius: 20px;
  background: $color4;
  height: 250px;
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}



.make-buttons-main {
  width: 100%;
  min-height: 100px;
  display: flex;
  justify-content: center;
  
  .buttons-wrapper {
    width: 50vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  
  .button-wrapper {
    width: 250px;
    height: 250px;
    background-image: url("../img/make-btn-bg.jpg");
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .button-wrapper:hover {
    transform: scale(1.15);
    transition: transform .2s ease-in;
  }
  
  .car-button {
    width: 215px;
  }
}



.services-main {
  width: 100%;
  display: flex;
  justify-content: center;
  
  .wrapper {
    width: 57vw;
    padding: 15px;
    min-height: 100px;
    background: $color5;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  #services-img {
    margin: 16px 10px 10px 0;
    float: left;
    width: 30vw;
  }
}



.maps {
  background: #a2a2a2;
  height: 450px;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  
  #map {
    width: 80vw;
    height: 400px;
    border-radius: 20px;
    pointer-events: none;
  }
}



.restoration-main {
  width: 100%;
  display: flex;
  justify-content: center;
  
  .wrapper {
    width: 57vw;
    padding: 15px;
    min-height: 100px;
    background: $color3;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    .img {
      max-height: 25vw;
      max-width: 25vw;
    }
    .left {
      float: left;
      margin: 16px 10px 10px 0;
    }
    .right {
      float: right;
      margin: 16px 0 10px 10px;
    }
  }
  
}



footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  
  .foot-orange {
    width: 100%;
    background-color: $color1;
    display: flex;
    justify-content: center;
  }
  .foot-contact {
    min-width: 440px;
    width: 60vw;
    max-width: 600px;
    display: flex;
    justify-content: space-between;
  }
}



/*
@media (max-width: ##px) {

}
*/

@media (max-width: 1150px) {
  #phone {
    display: none;
  }
  
}



@media (max-width: 1000px) {
  .services-main {
    
    .wrapper {
      width: 75vw;
    }
  }
  
  .restoration-main {
    
    .wrapper {
      width: 75vw;
    }
  }
  
}



@media (max-width: 550px) {
  .services-main {
    
    #services-img {
      width: 100%;
    }
  }
  
  .restoration-main {
    
    .img {
      max-width: 100%;
      max-height: none;
    }
  }
  
}